























import {Component, Vue} from 'vue-property-decorator';
import {Cell, CellGroup, Col, Field, Icon, List as VantList, Row} from 'vant';
import 'vant/lib/icon/style';
import 'vant/lib/cell/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import {CertOwn, Status} from '@/entry/diamond';
import {notes, NoteType} from '@/note';


Vue.use(Icon).use(Field).use(Row).use(Col).use(CellGroup).use(Cell).use(VantList);
@Component
export default class List extends Vue {
    private certs: CertOwn[] = [];
    private loading = true;
    private finished = false;
    private search = '';

    public created(): void {
        this.$store.state.title = this.$t('cert.list');
        this.loading = true;
        this.onLoad();
    }

    public get certsMap() {
        return this.certs.map((it: CertOwn) => {
          return {id: it.cert.getId(), ts: it.cert.ts.toString(), hasOwn: it.hasOwn()};
        });
    }

    public onClickSearch(): void {
      window.console.log('');
    }
    public onLoad(): void {
        this.$store.state.data.diamond.certList().then((certs: CertOwn[] | Status) => {
            this.loading = false;
            const tcerts = certs as CertOwn[];
            if (tcerts.map) { // have to check t is array by js
                this.certs = tcerts;
                this.finished = true;
            } else {
                notes.add({type: NoteType.Error, message: ''});
                this.finished = false;
            }
        }).catch((e: any) => {
            this.loading = false;
            notes.add({type: NoteType.Error, message: e.toString()});
        });
    }

    public onClickRow(id: string): void {
        const certOwn = this.certs.find((it) => (it.cert.getId() === id));
        if (certOwn) {
          this.$store.state.data.certOwn = certOwn;
          this.$router.push('/frame/cert/show');
        }
    }
}
